@import url("https://fonts.googleapis.com/css2?family=Muli:wght@300;400&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  --webkit-font-smoothing: antialiased;
}

.container {
  width: 1152px;
  max-width: 90%;
  margin: 0 auto;
}

h2 {
  text-align: center;
  font-size: 36px;
}

.formwrap {
  display: flex;
  justify-content: center;
}
.inputwrap {
  display: flex;
  align-items: center;
  /* border: 1px solid rgb(155, 66, 66); */
  margin-top: 20px;
  
}

.input {
  border: none;
  padding: 10px;
  font-size: 16px;
  min-width: 400px;
  outline: none;
  width: 100%;
  height: 50px;
  padding: 15px 22px;
  box-sizing: border-box;
  border: 4px solid #ccc;
  border-radius: 10px;
  background-color:white;
  resize: none; 
  
}
.button {
  border: none;
  background:#0093C4;
  color: #fff;
  height: 100%;
  font-size: 16px;
  padding: 0 20px;
  cursor: pointer;
  border-radius: 10px;
}
.data {
  max-width: 600px;
  margin: 0 auto;
  margin-top: 20px;
  text-align: center;
}
.loading {
  display: none;
}

.def {
  font-size: 20px;
  color: #050a13;
  line-height: 1.6;
}
.suggested {
  background: #b794f4;
  color: #fff;
  padding: 2px 10px;
  border-radius: 4px;
  margin-right: 10px;
  margin-top: 5px;
  display: inline-block;
}
.audio {
  margin-top: 20px;
}

.diction {
  border: 1px solid black;
  margin: 5px;
  padding: 5px;
  line-height: 60%;
  background-color: lightblue;
}
